.GlowCheckbox {
    width: 2.2rem;
    height: 2.2rem;
    background: #101010;
    border: 0.5px solid #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    margin-left: -0.5px;
    z-index: 10;
}

.GlowCheckbox-icon {
    width: 1.5rem;
    height: 1.5rem;
    box-sizing: content-box;
    border: 3px solid #000;
    border-radius: 50%;
    transition: 0.5s ease-out;
}

.GlowCheckbox-icon:hover {
    border: 1px solid #eee;
}

.GlowCheckbox-icon.checked {
    background: #000;
    border: 3px solid #fff;
    box-shadow: 0 0 10px rgba(33, 156, 243, 0.5),
        0 0 20px rgba(33, 156, 243, 0.5), 0 0 30px rgba(33, 156, 243, 0.5),
        inset 0 0 10px rgba(33, 156, 243, 1);
}

.GlowCheckbox-icon.checked:hover {
    background: #000;
    border: 3px solid #eee;
    box-shadow: 0 0 5px rgba(33, 156, 243, 0.5),
        0 0 10px rgba(33, 156, 243, 0.5), 0 0 15px rgba(33, 156, 243, 0.5),
        inset 0 0 5px rgba(33, 156, 243, 1);
}
